#connect-footer {
  background-color: #282c34;
  padding: 50px;
  text-align: center;
}

#connect-footer .section-header {
  text-align: center;
  margin-bottom: 40px;
}

#connect-footer .section-title {
  font-size: 36px;
  color: #f0f8ff;
  margin-bottom: 10px;
}

#connect-footer .section-tagline {
  font-size: 18px;
  color: #a9a9a9;
}

#connect-footer .section-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  color: #f0fff0;
}

#connect-footer a {
  color: #0066cc;
  text-decoration: none;
  border: 2px dashed #0066cc;
  padding: .5rem;
}

#connect-footer a:hover {
  color: cyan;
  border: 2px dotted cyan;
}

.footer-copyright {
  font-size: .7rem;
  color: #6a6a6a;
  padding-top: 20px;
}