/* Ramblings section */
#ramblings {
  background-color: #f7f7f7;
  padding: 50px;
}

.ramblings-header {
  text-align: center;
  margin-bottom: 40px;
}

.ramblings-title {
  font-size: 36px;
  color: #333;
  margin-bottom: 10px;
}

.ramblings-tagline {
  font-size: 18px;
  color: #666;
}

.blog-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.blog-card {
  min-width: 312px;
  background-color: #ffffff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  transition: transform 0.3s ease;
}

.blog-card:hover {
  transform: scale(1.02);
}

.featured-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.blog-info {
  padding: 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.author,
.date {
  font-size: 14px;
  color: #888888;
  margin-bottom: 5px;
}

.content {
  font-size: 16px;
  color: #333333;
}
