/* Styles for the Projects section */
.section-projects {
  background-color: #fefefe;
  padding: 50px 0;
}

#projects .section-header {
  text-align: center;
  margin-bottom: 40px;
}

#projects .section-title {
  font-size: 36px;
  color: #333;
  margin-bottom: 10px;
}

#projects .section-tagline {
  font-size: 18px;
  color: #666;
}

#projects .section-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
}

.col-1-3 {
  flex: 0 0 calc(33.3333% - 20px);
  margin: 10px;
}

.card-work {
  background-color: #f7f7f7;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.card-image img {
  width: 100%;
  height: auto;
}

.card-inner {
  padding: 10px;
}

.card-title {
  font-size: 18px;
  margin: 0;
  color: #333;
  text-decoration: none;
}

.card-title a {
  color: #333;
  text-decoration: none;
}

.card-title a:hover {
  color: #61dafb;
}

@media (max-width: 768px) {
  .col-1-3 {
    flex: 0 0 calc(50% - 20px);
  }
}

@media (max-width: 480px) {
  .col-1-3 {
    flex: 0 0 95%;
  }
}
