/* Header Styles */

.App-header {
  background-color: #282c34;
  min-height: 85vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  text-align: center;
}

/* Image Styles */

.image-container {
  text-align: right;
  margin-right: 100px;
}

.image-container img {
  width: 500px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Text Styles */

.text-container {
  text-align: left;
  margin-left: 50px;
  padding: 50px;
}

.text-container h1,
.text-container p {
  margin: 0;
  margin-bottom: 30px;
  font-weight: bold;
}

/* Highlighted Name */

.text-container .highlight {
  color: #ff0055;
  font-size: 1.2em;
  font-weight: bold;
}

/* Menu Styles */

.App-menu {
  background-color: black;
  padding: 20px;
  position: sticky;
  top: 0;
}

.App-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.App-menu li {
  margin: 0 10px;
}

.App-menu a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  padding: 10px;
  position: relative;
}

.App-menu a::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 10%;
  transform: translateX(0%);
  width: 0;
  height: 2px;
  background-color: #ff0055;
  transition: width 0.3s ease;
}

.App-menu a:hover::before {
  width: 80%;
}