@media (max-width: 912px) {
  .App-header {
    flex-direction: column;
  }

  .image-container {
    float: none;
    margin: 0 auto;
  }

  .image-container img {
    width: 280px;
    padding: 10px;
  }

  .text-container {
    text-align: center;
    margin: 20px auto;
  }

  .App-menu {
    display: none;
  }

